import Vue from 'vue';
import VueI18n from 'vue-i18n';

import th from './th';
import en from './en';

export default {
  locales: {
    en,
    th,
  },
  initial() {
    const self = this;
    Vue.use(VueI18n);
    Vue.config.lang = 'th';
    Object.keys(this.locales).forEach((lang) => {
      Vue.locale(lang, self.locales[lang]);
    });
  },
};



