                    <b>{{ $t("header.experienced2") }}</b>
                  </span>
                </h2>
              </section>
            </div>
            <div class="col-md-2 nopadding pdf">
              <a href="#" class="hvr-sweep-to-right"><i class="flaticon-linkedin22" title="My Linkedin Portfolio"></i></a>
            </div>
          </div>
          <!-- /Name-Position -->
          <!-- Main Navigation -->
