            <transition name="slide-home" appear>
              <li class="col-xs-4 col-sm-2 nopadding menuitem ui-menu-color01">
                <router-link to="/" class="hvr-sweep-to-bottom"><i class="flaticon-insignia"></i><span>home</span></router-link>
                <a href="#" class="hvr-sweep-to-bottom"></a>
              </li>
            </transition>
            <transition name="slide-resume" appear>
              <li class="col-xs-4 col-sm-2 nopadding menuitem ui-menu-color02">
                <router-link to="/" class="hvr-sweep-to-bottom"><i class="flaticon-graduation61"></i><span>resume</span></router-link>
