<style>
  .slide-home-enter-active,
  .slide-resume-enter-active,
  .slide-portfolio-enter-active,
  .slide-contact-enter-active,
  .slide-feedback-enter-active,
  .slide-blog-enter-active {
    transform: scale(1);
    transition: all 1s cubic-bezier(0.800, 0.300, 0.300, 0.800);
  }

  .slide-home-enter-active {
    transition-delay: .1s;
  }

  .slide-resume-enter-active {
    transition-delay: .3s;
  }

  .slide-portfolio-enter-active {
    transition-delay: .5s;
  }

  .slide-contact-enter-active {
    transition-delay: .7s;
  }

  .slide-feedback-enter-active {
